header {
  width: 80%;
  margin-left: 10%;
  margin-right: 10%;
  margin-bottom: 10px;
 }
 
 section {
   width: 80%;
   height: 800px;
   margin: auto;
   border: 1px solid blue;
 }

 #container {
  box-sizing: border-box;
  background-color: #e6e6e6;
  margin: auto;
  height: 800px;
  padding-bottom: 2.5rem;
  width: 70%;
  padding: 20px;
  float: left;
  overflow: auto;
}

aside {
  box-sizing: border-box;
  overflow: auto;
  background-color: #cccccc;
  padding: 20px;
  height: 800px;
  width: 30%;
  float: left;
}

@media screen and (max-width: 760px) {
  #container,
  aside {
    width: auto;
    float: none
  }
}

section:after {
  content: "";
  display: table;
  clear: both;
}

footer {
  border: 1px solid red;
  background-color: grey;
  color: white;
  text-align: center;
  height: 5.5rem;
  width: 80%;
  margin-left: 10%;
  margin-right: 10%;
  margin-top: 10px;
  margin-bottom: 10px;
}
